%title {
    font-family: $f-ssans;
    font-weight: 700;
    font-size: 24px;
    color: $c-primary;
}

%text {
    font-family: $f-ssans;
    font-weight: 400;
    font-size: 17px;
    line-height: 26px;
    color: $c-secondary;
}

%header {
    font-family: $f-ssans;
    font-size: 60px;
    line-height: 80px;
    letter-spacing: -0.5px;
    color: $c-primary;
}

%price {
    font-family: $f-ssans;
    font-weight: 300;
    font-size: 55px;
    letter-spacing: -0.5px;
    color: $c-primary;
}

%heading {
    font-family: $f-ssans;
    font-size: 60px;
    font-weight: 300;
    line-height: 80px;
    letter-spacing: -2px;
    color: $c-line;
}

%author-month-menu-genre {
    font-family: $f-ssans;
    font-style: italic;
    font-weight: 400;
    font-size: 12px;
    line-height: 58px;
    letter-spacing: 1.7px;
    text-transform: uppercase;
    color: $c-primary;
}

%small {
    font-family: $f-ssans;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 1.7px;
    text-transform: uppercase;
    color: $c-line;
}

%tags {
    line-height: 1;
}

