.alert{
    display: block;
    text-align: center;
    line-height: 3em;
    font-size: 14px;
    color: $c-primary;
    border: 1px solid transparent;
    margin-bottom: 20px;

    i{
        font-size: 140%;
        position: relative;
        top: 1px;
        margin-right: 5px;
    }
}
.alert--info{
    background-color: lighten($c-yellow, 30%);
    border-color: lighten($c-yellow, 30%);
}
.alert--success{
    background-color: lighten($c-green, 30%);
    border-color: lighten($c-green, 30%);
}
a.alert{
    @include transition(all $anim-time);

    &:hover{
        background-color: transparent;
        border-color: $c-link;
        color: $c-link;
    }
}