		margin-bottom: 40px;
	}
	.heading-2{
		font-family: $f-ssans;
		font-weight: 700;
		font-size: 20px;
		line-height: 1.25;
		margin-bottom: 10px;

		small{
			font-weight: 300;
			font-size: 80%;
		}
	}
	.heading-3{
		font-family: $f-ssans;
		font-weight: 300;
		font-size: 24px;
		line-height: 1.25;
		margin-bottom: 40px;
	}
	.heading-3--inText{
		padding-top: 30px;
		margin-bottom: 20px;
	}