$moduleName: "input";

// Def
	label{
		@extend %text;
		margin-bottom: 10px;
		display: block;
		color: $c-primary;
		cursor: pointer;

		&.error{
			color: red;
			position: absolute;
			margin-top: -30px;
			margin-bottom: 10px;
			background-color: white;
		}
	}
	input.error{
		border: 1px solid red !important;
	}
	.label-inline{
		float: left;
		line-height: 40px;
		margin-right: 20px;
	}
	.#{$moduleName}{
		@extend %input;
		@extend %text;
		font-weight: 700;
		display: block;
		line-height: 40px;
		height: 40px;
		width: 100%;
		border: 1px solid $c-line;
		color: $c-primary;
		background-color: white;
		padding: 0 10px;
		margin-bottom: 35px;
	}
	[type=radio]{
		float: left;		
		padding: 0;
		display: block;
		line-height: 40px;
		margin-right: 3px;
		height: 40px;
	}
