.tooltip {
    color: $c-link;
    position: relative;
    min-width: 10px;
    cursor: help;
    vertical-align: super;
    font-weight: 700;
    z-index: 3;

    &:hover .tooltip__content {
	    display:block;
	}
}
.tooltip__content{
	left: 5px; /* moves the tooltip to the right */
    top: 5px; /* moves it down */
    position: absolute;
    z-index: 666;
    display: none;
    font-weight: 400;
    min-width: 200px;
    padding: 10px 25px;
    font-size: 14px;
    background-color: #fff;
    @include box-shadow(2px 2px 5px #aaa);

    &:hover{
	    display: block;
	}
}