.prelunch
{
	background-color: $c-bg;
	text-align: center;

	.container{
		margin: 0 auto;
		max-width: 1340px;
		width: 100%;
		padding: 20px 30px;

		@media only screen and (min-width: 600px){
			padding-top: 80px;		
		}
	}
	.logopre{
		margin-bottom: 50px;
		display: block;
		text-align: center;

		img{
			width: auto;
			@media only screen and (min-width: 600px){width: 50%;margin: 0 auto;}	
		}
	}
	.main{
		margin-bottom: 20px;
		display: none;

		@media only screen and (min-width: 600px){display: block;}		
	}
	img{
		max-width: 100%;
		display: block;
	}

	h1{
		@extend %title;
		text-align: center;
		margin-bottom: 40px;
	}
	h2{
		@extend %title;
		text-align: center;
		font-weight: 300;
		margin-bottom: 40px;
		color: $c-green-d;	
	}
	footer{
		display: none;

		@media only screen and (min-width: 600px){display: block;}		

		img{
			width: auto;
		}
	}
}
.btn{
	@extend %articlesTitle;
	display: inline-block;
	line-height: 50px;
	padding: 0 30px;
	color: $c-bg;
	background-color: $c-green-d;
	margin-bottom: 40px;

	&:hover{
		background-color: transparent;
	}
}