.border{

}
.border-right{
	border-right: 1px solid $c-secondary;
}
.border-left{
	border-left: 1px solid $c-secondary;
}
.border-top{
	border-top: 1px solid $c-secondary;
}
.border-bottom{
	border-bottom: 1px solid $c-secondary;
}
.no-border{
	bottom: 0 none;
}

@include g-state( t ) {
	.border-left\@t{
		border-left: 1px solid $c-secondary;
	}
	.border-right\@t{
		border-right: 1px solid $c-secondary;
	}
}
@include g-state( d ) {
	.border-left\@d{
		border-left: 1px solid $c-secondary;
	}
}