// Def
	.header{		
		border-bottom: 3px solid $c-primary;
		border-top: 3px solid $c-primary;

		@include g-state( m ) {
			// border-bottom:1px solid $c-secondary;
		}
	}
	.header--popup{
		padding-top: 40px;
		height: 100px;
		padding-bottom: 40px;
		
		@include g-state( t d ) {
			padding-top: 60px;
			padding-bottom: 60px;
			height: auto;

			.logo{
				width: 25%;
			}			
		}
	}
	.header-btn
	{
		font-size: 40px;
		float: left;		
		line-height: 100px;
		padding: 0;

		span{
			@extend %author-month-menu-genre;
			height: 100px;
			vertical-align: text-bottom;
			position: relative;
			left: 20px;
			display: none;
		}
		
		@include g-state( t ) {
			font-size: 50px;
			margin-right: 30px;
			line-height: 99px;

			span{
				display: inline;
			}
		}
	}
	.header__nav{
		margin: 0 auto;
		display: block;
		line-height: 90px;
		text-align: left;
		
		@include g-state( d ) {
			width: 70%;
			text-align: center;

			ul{
				display: inline-block;
				margin-left: 34px;
			}
		}

		li{
			display: inline-block;			
			margin-left: 34px;

			&:first-child{
				margin-left: 0;
			}
		}
		a{
			@extend %author-month-menu-genre;
			display: block;			
			color: $c-primary;
			border-bottom: 2px solid transparent;
			@include transition(all $anim-time);

			&:hover{
				color: $c-link;
				border-bottom: 2px solid $c-primary;
			}
			&.is-current{
				border-bottom: 2px solid $c-primary;
			}
		}
		&:after{
			display: table;
			content: '';
			clear: both;
		}
	}
	.header__user{
		float: right;
		display: none;

		@include g-state( t d  ) {
			display: block;
		}
	}
	.header__login
	{
		@extend %author-month-menu-genre;
		float: right;
		display: none;
		padding: 15px 0;
		line-height: 60px;

		@include g-state( t d  ) {
			display: block;
		}

		&:hover{
			color: $c-link;
		}
	}
	.header__headIcon{
		float: right;
		color: $c-primary;
		font-size: 25px;
		margin-top: 37px;
	}