.author{
	@extend %small;
	margin-bottom: 0;
	display: inline-block;
}
a.author:hover{
	color: $c-link;	
}
.author-head
{
	display: block;
	width: 90px;
	height: 90px;
	margin: 0 auto 20px;
	border: 1px solid $c-secondary;
	overflow: hidden;	
	@include border-radius(90px);

	img{
		display: block;
		width: 90px;
		height: 90px;
		@include border-radius(90px);
	}
}
.author__name
{
	@extend %small;
	padding-top: 20px;
	text-align: center;
	color: $c-link;

	&:hover{
		color: $c-primary;
	}
}