.section{
	padding-bottom: 60px;
}
.section-pageLinks{
	@extend %author-month-menu-genre;
	line-height: 60px;
}

// header
	.section-header{
		height: 61px;
		line-height: 61px;
		border-bottom: 1px solid $c-secondary;
		border-top: 2px solid $c-line;
		margin-bottom: 60px;
		position: relative;
	}
	.section-header--fliped{
		border-top: 1px solid $c-secondary;
		border-bottom: 2px solid $c-line;

	}
	.section-header--center{
		.section-header__tabs{
			@include g-state( d ) {
				left: 50%;
				@include transform(translate(-50%, 0));
			}
		}
	}
	.section-header__name{
		@extend %sectionTitle;		
	}
	.section-header__tabs{
		position: absolute;
		left: 0;
		bottom: 0;

		li{
			@extend %tabs;
			position: relative;
			float: left;
			text-align: center;
			height: 58px;			
			width: 150px;
			border: 1px solid transparent;
			border-bottom: 0 none;
			margin-top: 8px;
			bottom: -1px;
			z-index: 1;
			@include border-radius(3px 3px 0 0);
			@include transition(all $anim-time);

			&:hover{
				z-index: 2;				
				// border-bottom: 0 none;
				// height: 66px;
				// background-color: $c-bg;
				// margin-top: 0;
			}
			&.is-active{
				z-index: 2;								
				height: 66px;
				background-color: $c-bg;
				margin-top: 0;
				border: 1px solid $c-secondary;
				border-bottom: 0 none;
			}

			@include g-state( t ) {
				width: 220px;				
			}
		}
		a{
			@extend %tabs;
			position: absolute;
			width: 100%;
			left: 0;
			text-align: center;
			height: 58px;
			bottom: 0;
			line-height: 58px;
		}
	}