$moduleName: "socials";

// Def
	.#{$moduleName}{
		display: block;
		text-align: center;
		margin-bottom: 20px;

		li{
			display: inline-block;
			line-height: 40px;
			height: 40px;
			width: 40px;
			margin: 10px 10px;
		}
	}
	.#{$moduleName}--scaled{
		li{
			@include transform(scale(0.8,0.8));
			margin: 10px 0px;
			i{
				font-size: 18px;
			}
		}
	}
