$moduleName: "selfcare";

// Def
	.#{$moduleName}
	{
		border: 2px solid $c-line;
		border-left: 0 none;
		border-right: 0 none;
		position: relative;
		background-color: $c-bg-dark;
		margin-bottom: 50px;

		[type=text],[type=password],[type=number], [type=email], [type=tel]{
			@extend .input;

			&:disabled{
				background-color: $c-bg-dark;
			}
		}
		@include g-state(d){
			border: 2px solid $c-line;
		}
	}	
	.#{$moduleName}__logreg
	{
		position: relative;
		padding: 40px 50px;
	}
	.#{$moduleName}__content{
		background-color: #fff;
		padding: 40px 25px 10px;		

		@include g-state(d){
			padding: 40px 50px 10px;
			margin-left: -25px;
			min-height: 250px;
		}
	}
	.#{$moduleName}__companyInfo{
		display: none;
	}
	.#{$moduleName}__form
	{
		position: relative;
	}
	.#{$moduleName}__whitter
	{
		background-color: #fff;
	}
	.#{$moduleName}
	{
		position: relative;
	}.#{$moduleName}
	{
		position: relative;
	}
	.#{$moduleName}
	{
		position: relative;
	}