// Def
	.footer{
		padding-bottom: 50px;

		p{
			color: $c-secondary;
		}
	}
	.footer__right{
		text-align: center;

		@include g-state(d){
			float: right;
			text-align: right;
		}
	}
	.footer__left{
		text-align: center;

		@include g-state(d){
			float: left;
			text-align: left;
		}
	}
	.footer__nav{
	
	}
	.copyright{
		@extend %small;
		
	}
	.footer-links{
		background-color: $c-primary;
		text-align: center;
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		min-height: 150px;
		padding-top: 30px;
	}


	.footer-logos
	{
		padding-top: 30px;
		text-align: center;

		a{
			margin: 0;
			padding: 0 10px 10px;
			display: inline-block;
			width: 100%;

			@include gridle-state(t){
				width: 24%;
			}
		}
		img{
			max-width: 100%	
		}
	}