$moduleName: "tagFilter";

.#{$moduleName}{
	position: relative;
	border-bottom: 1px solid $c-line-thin;
	
	.btn{
		display: none;
	}
	i{
		position: absolute;
		right: 0;
		font-size: 30px;
		color: $c-blue;
	}
	@include gridle-state(t){
		padding: 35px 15px 30px;
		text-align: center;
		border: 1px solid $c-line-thin;

		.btn{
			display: inline-block;
			width: 100%;
		}
		&:after {
			content: '';
			background-image: url('../images/floatingShadow.png');
			background-repeat: no-repeat;
			background-position: center 0;
			background-size: 100% 100%;
			width: 100%;
			height: 70px;
			left: 0;
			position: absolute;
			bottom: -45px;
			z-index: -1;
		}
	}
}
.#{$moduleName}__img{
	display: block;
	position: relative;
	float: right;
	margin-right: 27px;
	text-align: center;
	width: 85px;
	height: 75px;

	img{
		display: block;
		max-width: 100%;
		max-height: 100%;
		position: absolute;
		left: 50%;
		top: 50%;
		@include transform(translate(-50%, -50%));
	}

	@include gridle-state(t){
		float: none;
		width: auto;
		height: auto;
		text-align: center;
		margin-right: 0;
		margin-bottom: 20px;

		img{
			position: static;
			@include transform(translate(0, 0));
		}
	}
}
.#{$moduleName}__title{
	
}
.#{$moduleName}__desc{
	font-size: 10px;
	color: $c-secondary;
	line-height: 11px;
	text-align: left;
}
