$moduleName: "basket";
// Def
.#{$moduleName} {
    position: relative;
    button+button {
        margin-right: 20px;
    }
    @include g-state(t) {
        padding: 10px 50px;
        border: 2px solid $c-line;
    }
    @include g-state(d) {
        min-width: 1150px;
    }
}

.#{$moduleName}--count {
    @extend .basket;
    border-top: 1px solid;
    margin-bottom: 50px;
    @include g-state(t) {
        padding: 10px 50px 30px;
    }
}

.#{$moduleName}__address {
    p {
        font-size: 14px;
        font-weight: 700;
        line-height: 1.5;
        text-align: center;
    }
    span {
        color: $c-secondary;
        font-weight: 600;
        font-size: 20px;
    }
    @include g-state(d) {
        p {
            text-align: left;
        }
        span {
            font-size: 1em;
        }
    }
}

.#{$moduleName}__additional {
    text-align: center;
    @include g-state(t) {
        text-align: left;
    }
}

.#{$moduleName} {
    position: relative;
}

.#{$moduleName} {
    position: relative;
}

