// Def
	.l-sponsor{
		@include g-state( d ) {
			position: relative;
			margin-top: -60px;
			z-index: 1;
			// margin-bottom: 15px;
		}
	}
	.sponsor{
		border-bottom: 1px solid $c-secondary;
		border-top: 1px solid $c-secondary;
		height: 62px;
		line-height: 62px;
		position: relative;
		
		@include g-state( d ) {
			border-top: 0 none;
			height: 66px;
			line-height: 66px;
		}
		
	}
	.sponsor__logo{
		display: block;
		height: 62px;
		float: right;
		text-align: center;		
		padding: 11px 10px;

		img{
			display: inline-block;
			height: 40px;
		}

		@include g-state( d ) {
			padding: 5px 10px;
			border: 1px solid $c-secondary;
			@include border-radius(3px 3px 0 0);
			border-bottom: 0 none;
			height: 66px;
			position: relative;
			bottom: 0px;
			border-bottom: 1px solid $c-bg;
			width: 170px;
			z-index: 2;			

			img{				
				height: 25px;	
			}
		}
	}
	.sponsor__text{
		@extend %author-month-menu-genre;
		font-size: 10px;
		line-height: 62px;
		float: left;

		@include g-state( d ) {			
			float: none;			
			position: absolute;
			right: 0;
			bottom: 0px;
			line-height: 1;
			text-align: center;
			width: 170px;
			color: $c-secondary;
		}
	}