$moduleName: "nav";
// External modificators from _st-components
.st-menu-open {
    .#{$moduleName} {
        display: block;
    }
}

// Def
.#{$moduleName} {
    position: relative;
    display: none;
    padding: 110px 15px 0 45px;
    h2 {
        @extend %title; // thirdTitle; // articleTitle
        margin-bottom: 10px;
        font-weight: 300;
        color: white;
    }
    li {
        line-height: 2em;
        display: block;
    }
    a,
    button {
        @extend %text;
        color: white;
        display: block;
        &:hover,
        &:focus {
            color: $c-link;
        }
    }
    .is-current {
        color: $c-link;
        font-weight: 600;
    }
    .#{$moduleName}__iconLink {
        line-height: 48px;
        @include transition(all $anim-time);
        &:active {
            color: white;
        }
        i {
            float: right;
            line-height: 48px;
            font-size: 22px;
        }
    }
}

.#{$moduleName}__list {
    display: block;
    margin-bottom: 20px;
    font-size: 14px;
    i {
        font-size: 10px;
        margin-left: 5px;
    }
}

.#{$moduleName}__close {
    position: absolute;
    top: 36px;
    left: 45px;
    font-size: 22px;
    color: white;
}

.#{$moduleName}-borderedBox {
    min-height: 50px;
    border-bottom: 1px solid white;
    border-top: 1px solid white;
    margin-bottom: 20px;
}

.#{$moduleName}__iconLink {
    line-height: 48px;
    &:active {
        color: white;
    }
    i {
        float: right;
        line-height: 48px;
        font-size: 22px;
    }
}

.#{$moduleName}__manufacturer {
    text-align: center;
    font-size: 12px;
    color: white;
    padding-top: 40px;
    margin-bottom: 45px;
    padding-right: 30px;
    a {
        color: $c-link;
        font-size: 12px;
        // font-style: italic;
    }
}

.nav-shop {
    @extend %text;
    position: relative;
    line-height: 80px;
    height: 80px;
    text-align: center;
    border-bottom: 1px solid $c-line;
    margin-bottom: 80px;
    h3 {
        @extend %title;
    }
    i {
        margin-right: 20px;
    }
    @include g-state(t) {
        margin-bottom: 50px;
        a {
            position: absolute;
            left: 0;
            float: left;
        }
    }
}

.#{$moduleName}-login {
    display: none;
    padding-bottom: 20px;
    [type="email"],
    [type="password"] {
        display: block;
        line-height: 34px;
        height: 36px;
        border: 1px solid #5a646e;
        padding: 0 10px;
        margin-bottom: 15px;
        width: 100%;
        @include transition(all $anim-time);
        &:invalid {
            border: 1px solid red;
        }
    }
    [type="submit"] {
        display: block;
        border: 0 none;
        text-align: center;
        height: 36px;
        line-height: 36px;
        text-transform: uppercase;
        color: white;
        background-color: $c-link;
        width: 100%;
        margin-bottom: 15px;
        font-weight: 700;
        letter-spacing: 1px;
        @include transition(all $anim-time);
        &:hover,
        &:focus {
            background-color: #fff;
            color: $c-link;
        }
    }
    &--nbp {
        padding-bottom: 0;
        [type=submit] {
            margin-bottom: 0;
            cursor: pointer;
        }
    }
    .forgotPassword {
        font-size: 14px;
    }
}