$moduleName: "datePicker";

// Def
	.#{$moduleName}{
		position: relative;		

		&:after{
			content:'';
			display: table;
			clear:both;
		}
	}
	.#{$moduleName}__date{
		@extend %articlesTitle;
		display: block;
		line-height: 58px;
		width: 100%;		
		background-color: transparent;
		padding: 0 10px;
		border: 0 none;
		margin-bottom: 0;
		cursor: pointer;
	}
	.#{$moduleName}__picker{
		@extend %articlesTitle;
		color: $c-link;
		float: right;
		text-align: left;
		font-size: 13px;
		position: relative;
		position: absolute;
		line-height: 60px;
		top: 0;
		right: 15px;

		i{
			margin-left: 15px;			
		}
	}
	.picker{
		top:30px;
	}
