$moduleName: "cart";

// Def
	
	.#{$moduleName}{
		@extend %text;
		color: $c-link;
		float: right;
		line-height: 90px;
		@include transition(all $anim-time);

		i{
			font-size: 42px;
		    position: relative;
		    top: 10px;
		    @include transition(all $anim-time);
		}
	}
	.#{$moduleName}__count{
		margin-right: 10px;
	}