.dropdown
{
	@extend %articlesTitle;
	line-height: 60px;	
	float: left;
	position: absolute;
	left: 0;
	z-index: 1;	
	height: auto;
	width: auto;
	min-width: 50px;
	// @include box-shadow(0 1px 3px #aaa);
	@include transition (all $anim-time);

	&:after{
		font-family: "Ionicons";
		content: '\f123';
		position: absolute;
		right: 25px;
		font-weight: 300;
		top: 0;
		font-size: 12px;
	}

	&:hover{
		background-color: $c-bg;
		background-color: rgba(251,251,226,0.9);

		li{
			display: block;
		}
	}

	li{		
		line-height: 60px;
		display: none;
		width: auto;
		min-width: 50px;
		@include transition (all $anim-time);

		&.is-active{
			display: block;

			// .dropdown__active{
			// 	visibility: visible;
			// }
		}		
	}
	a{
		color: $c-primary;
		display: inline-block;
		height: 60px;
		padding-left: 15px;
		padding-right: 50px;
		background-color: transparent;
		@include transition (all $anim-time);
		
		&:hover{
			color: $c-link;
			background-color: $c-bg;
		}
	}
}
.dropdown__active{
	display: inline;
	// visibility: hidden;
}
.dropdown--right
{
	position: relative;
	left: auto;
	right: 0;
	float: right;

	&:after{
		right: 0;
	}
}