.support-msg {
  display: none;
}

.specifiedProduct {
  margin-bottom: 35px;
  margin-top: -20px;
}

.msg-required-mark {
  display: none;  
}

.selfcare__content--support {
  margin-bottom: 80px;

  .submit-wrap {
    height: 100%;
    margin: 0;
    padding: 35px 0;
  }

  .section-separator {
    height: 80px;
  }

  .section-separator + .heading-3 {
    margin: 40px 0;
  }
}

.support-form label.error {
    color: red;
    margin: 0;
    position: inherit;
    background-color: white;
    display: block;
    padding: 10px;
}

.problemType, .productType {
    margin-bottom: 35px;
}

.callMePreference {
  margin-top: 15px;
}

.callMePreference + label {
  display: inline-block;
}

.textarea--support {
  display: block;
  line-height: 30px;
  width: 100%;
  border: 1px solid $c-line;
  color: $c-darkblue;
  background-color: white;
  padding: 0 10px;
  margin-bottom: 35px;
  max-width: 100%;
  font-size: 17px;
}

