
@import "../gridle/gridle/gridle";

/* me: iZ fakt */
$settings : (
    context : 12,           // number of columns in your grid
    column-width : null,    // by default it's percentage based depending on the context but you can specify a column width yourself
    gutter-width : 50px,    // size of the gutters
    gutter-height : 0,      // size of top and bottom gutters
    gutter-top    : 0,      // size of top gutter
    gutter-bottom : 0,      // size of bottom gutter
    gutter-left   : 25px,   // size of left gutter
    gutter-right  : 25px,   // size of right gutter
    direction : ltr,        // direction
    dir-attribute : false,  // if need to generate support for dir="rtl" in order to set part of layout direction
    states-classes : false, // if we need to generate the states classes
    name-multiplicator : 1, // if 5, generate gr-5, gr-10, etc...
    classes-prefix : 'l-'     // the prefix used for all classes 
);
@include gridle_setup($settings);

@include gridle_register_state( m, (
    query: "only screen and (max-width: 599px)",    
) );
@include gridle_register_state( t , (
    query: "only screen and (min-width: 768px), screen and (min-width: 600px) and (max-width: 1024px) and (orientation: portrait), screen and (orientation: landscape)",    
) );
@include gridle_register_state( d, (
    query: "only screen and (min-width: 1025px), screen and (orientation: landscape), print",    
) );

@include gridle_init();

@include gridle_generate_classes();

[class*="l-gr-"] {	
	position: relative;

    // @include g-state( m ) {
    //     @include gridle_grid( 12 );        
    // }
}
.l-row{
	position: relative;
}

// todo: kazdy state ma jiny pocet sloupcu

.l-wrapper{
    margin: 0 auto;
    padding: 0 20px;
    min-width: 320px;

    @include g-state( t ) {
        max-width: 1024px;
        min-width: 768px;
        width: 100%;
        padding: 0 50px;
    }
    @include g-state( d ) {
        width: 1390px;
        max-width: 1390px;
        min-width: 1390px;        
        padding: 0 60px;
    }
}
.l-center-VH
{
	position: absolute;
	top: 50%;
	left: 50%;
	@include transform(translate(-50%, -50%))
}