$moduleName: "order";
// Def Home
.#{$moduleName} {
    /*margin-bottom: 40px;*/
    /*padding-bottom: 20px;*/
    border-bottom: 1px solid $c-line;
    @include g-state(t) {
        .btn-def {
            float: right;
        }
    }
}

.#{$moduleName}__header {
    position: relative;
    height: 100px;
    line-height: 50px;
    background-color: $c-bg-dark;
    @include transition(all $anim-time);
    &:hover {
        background-color: $c-link;
        color: white;
        cursor: pointer;
        a {
            text-decoration: underline;
        }
    }
    a {
        font-weight: 700;
    }
    i {
        position: absolute;
        line-height: 10px;
        right: 10px;
        top: 50px;
        &:after {
            vertical-align: baseline;
        }
        @include g-state(t) {
            display: none;
        }
        @include g-state(d) {
            display: block;
        }
    }
    @include g-state(t) {
        height: 50px;
        i {
            top: 20px;
        }
    }
}

.order--notPayed {
    .#{$moduleName}__header {
        background-color: lighten($c-yellow, 30%);
        &:hover {
            background-color: $c-yellow;
            a {
                //
            }
        }
    }
}

.order--opened {
    &.order--notPayed {
        .#{$moduleName}__header {
            background-color: $c-yellow;
        }
    }
    .#{$moduleName}__header {
        background-color: $c-link;
        color: white;
    }
    i {
        @include transform(rotate(180deg));
    }
}

.#{$moduleName}__body {
    display: none;
    padding: 20px 10px 10px;
    background-color: white;
    @include g-state(m) {
        .btn-def {
            width: 100%
        }
    }
}

.#{$moduleName}__footer {
    padding: 30px 0;
    p {
        line-height: 40px;
        margin-bottom: 0;
        text-align: center;
    }
    hr {
        margin-bottom: 20px;
    }
    @include g-state(t) {
        p {
            text-align: left;
        }
    }
}