$moduleName: "harmonic";

// Def
	.#{$moduleName}
	{	

	}		
	.#{$moduleName}__header
	{
		position: relative;
		cursor: pointer;
		padding-top: 20px;
		padding-bottom: 5px;
		// border-top: 1px solid $c-line;
		border-bottom: 1px solid $c-line;

		&.no-padding{
			padding-top: 0;
		}

		&.is-active{
			cursor: default;
			border-top: 0 none;
			border-bottom: 0 none;
			padding-bottom: 0;

			.heading-3{
				margin-bottom: 40px;
				color: $c-primary;
			}

			.#{$moduleName}__subtitle{
				display: none;
			}

			& + .#{$moduleName}__content{	
				border-bottom: 1px solid $c-line;				
			}
		}

		.heading-3{
			margin-bottom: 0px;
			color: $c-link;
		}
	}
	.#{$moduleName}__title
	{
		position: relative;
	}
	.#{$moduleName}__subtitle
	{
		@extend %text;
		color: $c-line;
		display: block;
		position: relative;
	}
	.#{$moduleName}__content
	{				
		position: relative;
	}