$moduleName: "table";
// Def Home
.#{$moduleName} {
    width: 100%;
    margin-bottom: 20px;
    th {
        font-weight: bold;
    }
    td {
        word-break: break-word;
    }
    td,
    th {
        min-width: 70px;
        padding: 5px 10px;
        border-bottom: 1px solid $c-line;
        line-height: 2em;
    }
}

.is-deleting {
    td {
        background-color: $c-red;
        @include transition(all, $anim-time);
    }
}