			text-decoration: none;
		}
	}		
}
p
{   
	@extend %text;
	color: $c-primary;
	margin-bottom: 1em;
	
    a
	{  
		color: $c-link;

	  	&:hover //;
	  	{
	  	   color: $c-primary;
	  	   text-decoration: none;
	  	}
	  	&:focus //;
		{
			text-decoration: none;
		}
	}
	.a-reversed{
		color: $c-link;

	  	&:hover //;
	  	{
	  	   color: $c-link;
	  	   text-decoration: none;
	  	}
	}

}
ul{
	li{
		line-height: 1.35;
		margin-bottom: 5px;
	}
}
img{max-width: 100%;}
a
{  
  text-decoration: none;  
  color: $c-link;
  @include transition(color $anim-time, background-color $anim-time, border-color $anim-time);
  
  &:hover //;
  {
      text-decoration: none;
      color: $c-primary;
  }

  &:focus //;
  {
	text-decoration: none;
  }

  span{
  	 @include transition(color $anim-time, background-color $anim-time);
  }
}
// FF fix on borders on hyperlinks
:focus {outline:none;}
::-moz-focus-inner {border:0;}

strong{
	font-weight: 700;
}
b{
    font-weight: 700;
}
em{
	font-style: italic;
}
sup{
	vertical-align: super;
	font-size: smaller;
}
sub{
	vertical-align: sub;
	font-size: smaller;
}
.hr{
	margin-bottom: 20px;
	border-bottom: 1px dashed $c-line;
	background-color: transparent;
	display: block;
}
small{
	font-size: 85%;
}
button{
	display: block;
	background-color: transparent;
	border: 0 none;
	cursor: pointer;
	@include transition(all $anim-time);

	&:hover{
		color: $c-link;
	}
}
.fw-300{
	font-weight: 300;
}	
[type=text], [type=password], textarea
{

	padding: 0;
	border: 0 none;
	outline: 0 none;
	font-family: 'source-sans-pro', 'Source Sans Pro', Arial, 'Liberation Sans', Helvetica; font-weight: 400;

	&::-webkit-input-placeholder {
	
	}

	&:-moz-placeholder { /* Firefox 18- */
	  
	}

	&::-moz-placeholder {  /* Firefox 19+ */
	 
	}

	&:-ms-input-placeholder {  
	   
	}
}
[type=text], [type=password]
{

}
[type=submit]
{
	@include experimental(appearance, none);
}
input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
textarea{
	
}

.is-error, :invalid, :-moz-ui-invalid {
    border-color: $c-red !important;
    outline:none;
    box-shadow:none;
    -moz-box-shadow:none;
    -webkit-box-shadow:none;
}
::-ms-clear
{
    display: none;
}
::-ms-reveal
{
    display: none;
}
.clear{
	clear: both;
}
.underline
{
	text-decoration: underline;
}
.move-right{
	float: right;
}
.move-left{
	float: left;
}
.move-down{
	margin-top: 25px;
}
.align-center{
	text-align: center !important;
}	
.align-left{
	text-align: left !important;
}	
.align-right{
	text-align: right !important;
}

.no-margin
{
	margin: 0 !important;
}
.no-margin-bottom{
	margin-bottom: 0 !important;
}
.dotdotdot
{
	overflow: hidden;
	text-overflow: ellipsis;
}
.margin-bottom-30{
	margin-bottom: 50px;
}
.margin-bottom-40{
	margin-bottom: 50px;
}
.margin-bottom-50{
	margin-bottom: 50px;
}