$moduleName: "card";
// Def
.#{$moduleName} {
    position: relative;
    padding: 35px 15px 30px;
    border: 1px solid $c-line-thin;
    // height: 400px;
    margin-bottom: 90px;
    text-align: center;
    background-color: #fff;
    img {
        display: block;
        margin: 0 auto;
        max-height: 100%;
    }
    &:after {
        content: '';
        background-image: url('../images/floatingShadow.png');
        background-repeat: no-repeat;
        background-position: center 0;
        background-size: 100% 100%;
        width: 100%;
        height: 70px;
        left: 0;
        position: absolute;
        bottom: -45px;
        z-index: -1;
    }
}

.#{$moduleName}--bH {
    padding: 31px 14px;
    border: 2px solid $c-line-thick;
}

.#{$moduleName}--bC {
    border-color: $c-red;
}

.#{$moduleName}__img {
    position: relative;
    display: block;
    margin: 0 -15px 20px;
    height: 157px;
    overflow: hidden;
    // max-height:235px;
}

.#{$moduleName}__title {
    @extend %title;
    margin-bottom: 20px;
    font-size: 36px;
}

.#{$moduleName}__subtitle {
    @extend %title;
    font-weight: 300;
    margin-bottom: 30px;
}

.#{$moduleName}__desc {
    @extend %text;
    // color: $c-primary;
    margin-bottom: 15px;
    max-height: 54px;
    height: 54px;
    overflow: hidden;
}

.#{$moduleName}__price {
    @extend %price;
    margin-bottom: 10px;
}

.#{$moduleName}__ribbon {
    position: absolute;
    left: -35px;
    top: -25px;
    display: block;
    width: 76px;
    height: 68px;
    background-image: url('/templateAssets/images/masle.png');
    z-index: 1;
}

.#{$moduleName}__flag {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    width: 0;
    height: 0;
    z-index: 1;
    border-style: solid;
    border-width: 0 120px 120px 0;
    border-color: transparent rgba(255, 240, 0, 0.7) transparent transparent;
    // @include opacity(0.7);
    img,
    svg {
        position: absolute;
        display: block;
        width: 120px;
        height: 120px;
        left: 0;
        top: 0;
        z-index: 2;
    }
}
