$moduleName: "article";
// universal
	.genre{
		@extend %small;
		line-height: 50px;
	}
	.small{
		@extend %small;
		line-height: 20px;
	}
// Def Home
	.#{$moduleName}-home
	{
		position: relative;
		margin-bottom: 30px;

		@include g-state( t ){
		}
	}
	.#{$moduleName}-home__genre{		
		margin-bottom: 30px;
		color: $c-primary;
	}
	.#{$moduleName}-home__title{
		@extend %title;
		margin-bottom: 15px;

		a{
			color: $c-primary;
		}
	}
	.#{$moduleName}-home__img{
		display: block;
		margin-bottom: 10px;
	}
	.#{$moduleName}-home__caption{
		@extend %small;
		text-align: center;
	}
	.#{$moduleName}-home__date{
		border-top: 1px solid $c-secondary;
		padding-top: 20px;
		text-align: center;
		margin-left: 60px;
	}
	.#{$moduleName}-home__dateDay{
		@extend %sectionTitle;
		margin-bottom: 20px		
	}
	.#{$moduleName}-home__dateMonthYear{
		@extend %small;	
	}
	.#{$moduleName}-home__tags{
	}

// Koment Long
	.#{$moduleName}-koment-long
	{
		position: relative;
		margin-bottom: 50px;

		@include g-state( t ){
			.perex{
				max-height: 110px;
				overflow: hidden;
			}			
		}
	}
	.#{$moduleName}-koment-long__title{
		@extend %secondTitle;
		margin-top: 5px;
		margin-bottom: 15px;

		a{
			color: $c-primary;
		}
		@include g-state( t ){
			max-height: 75px;
			overflow: hidden;
		}
	}	

// Koment Long
	.#{$moduleName}-koment-short
	{
		position: relative;
		margin-top: 60px;

		&:first-child{
			margin-top: 0;
		}
	}
	.#{$moduleName}-koment-short__title{
		@extend %articlesTitle;
		margin-top: 5px;
		margin-bottom: 10px;

		a{
			color: $c-primary;
		}

		@include g-state( t ){
			max-height: 60px;
			overflow: hidden;
		}
	}
// Koment Small
	.#{$moduleName}-koment-small
	{		
		position: relative;
		margin-bottom: 30px;
	}
	.#{$moduleName}-koment-small__title{
		@extend %thirdTitle;
		margin-bottom: 15px;
	}

// Article Photo
	.#{$moduleName}-photo
	{
		position: relative;
		margin-top: 20px;
		margin-bottom: 30px;
		
		@include g-state( d ){
			margin-bottom: 0;
		}

		&:first-child{
			margin-top: 0;
		}
	}
	.#{$moduleName}-photo__image{
		display: block;

		img{
			width: 100%;
		}
	}
	.#{$moduleName}-photo__title{
		@extend %articlesTitle;
		margin-bottom: 15px;
	}	
//  Article Photo small
	.#{$moduleName}-photo--small
	{
		img{
			margin-bottom: 5px;
		}
		
		.#{$moduleName}-photo__title{
			@include g-state( t d ){
				height: 100px;
				overflow: hidden;
			}
		}
	}
// Article bm
	.#{$moduleName}-bm24
	{
		position: relative;
		margin-bottom: 50px;
	}
	.#{$moduleName}-bm24__image{
		display: block;

		img{
			width: 100%;
		}
	}
	.#{$moduleName}-bm24__title{
		@extend %secondTitle;		
	}	

// Rozhovor 
	.#{$moduleName}-rozhovor
	{
		position: relative;
		margin-top: 20px;

		&:first-child{
			margin-top: 0;
		}
	}
	.#{$moduleName}-rozhovor__image{
		display: block;

		img{
			width: 100%;
		}
	}
	.#{$moduleName}-rozhovor__title{
		@extend %secondTitle;
		margin-bottom: 15px;
	}	
	.#{$moduleName}-rozhovor__genre{
		font-weight: 700;
	}
	.#{$moduleName}-rozhovor__perex{
		@include g-state( t d ){
			height: 88px;
			overflow: hidden;
		}
	}
// Salón
	.#{$moduleName}-salon
	{
		position: relative;
	}
	.#{$moduleName}-salon__img{
		margin-bottom: 50px;
		display: block;
	}
	.#{$moduleName}-salon__title
	{
		@extend %articlesTitle;
		margin-bottom: 15px;
	}
	.#{$moduleName}-salon__guests{
		@extend %small;
		font-weight: 700;
		margin-bottom: 35px;
		display: block;
	}
	.#{$moduleName}-salon__guest{
		@extend %small;
		margin-bottom: 9px;
	}
	.#{$moduleName}-salon__midle{
		@include g-state( t d ){
			min-height: 318px;
		}
	}


// Landing page
	.#{$moduleName}-detail
	{
		position: relative;	
		padding-top: 50px;
		@extend %articleText;		
	}
	.#{$moduleName}-detail__title
	{
		@extend %title;
		margin-bottom: 50px;
	}
	.#{$moduleName}-detail__mainImg
	{
		img{
			width: 100%;
		}
	}
	.#{$moduleName}-detail__caption{
		@extend %author-month-menu-genre;
		text-align: center;
		margin-bottom: 30px;
	}

	.#{$moduleName}-detail__body
	{
		
	}
	.#{$moduleName}-detail__genre
	{
		margin-bottom: 30px;
		line-height: 20px;
		
		@include g-state('d'){
			line-height: 50px
		}
	}
	.#{$moduleName}-detail__perex
	{
		@extend %articlePerex;
		margin-bottom: 2em;
	}
	.#{$moduleName}-detail__date{
		@extend %small;
		overflow: hidden;

		@include g-state('d'){
			margin-bottom: 60px;
		}
	}
	.#{$moduleName}-detail__body-left{
		@include g-state('t'){
			.author{
				display: block;
				float:left;
				margin-bottom: 50px;
			}
		}

		@include g-state('d'){
			padding-top: 80px;

			.author{
				float: none;				
			}
		}
	}
	.#{$moduleName}-detail__content{
		margin-bottom: 50px;
		
		@include g-state('d'){
			border-right: 1px solid $c-secondary;	
		}
	}
	.#{$moduleName}-detail__texts{
		& > p{
			@extend %articleText;
		}	
		& > .perex{
			font-weight: 700;
			margin-bottom: 2em;
		}
	}
	.#{$moduleName}-detail__img{
		@include g-state('t'){
			width: 830px;
			padding-bottom:30px;
			padding-top: 30px;
		}
		
		@include g-state('d'){			
			padding-bottom:40px;
			padding-top: 50px;
			border-right: 1px solid $c-bg;
			text-align: center;
		}

		figcaption{
			@extend %small;
		}
	}

	blockquote{
		@extend %blockquote;
		padding: 25px 0;

		p{
			@extend %blockquote;
		}
	}