.pw-button{
	border: 1px solid #c8c8b4;
    padding: 0;
    border-radius: 40px;
    width: 40px !important;
    height: 40px !important;
    position: relative;
    padding-bottom: 0 !important;
    margin: 0 auto 20px !important;
    float: none !important;

    :before{
		position: absolute;
		font-size: 36px;
		color: #c8c8b4;
		left: 50%;
		top: 50%;
		line-height: 42px;
		vertical-align: middle;
		@include transform(translate(-50%,-50%));
	}
}
