$moduleName: "logo";

// Def
	.#{$moduleName}{
		position: absolute;
		left: 50%;
		top: 50%;
		width: 40%;
		z-index: 2;
		margin-bottom: 10px;
		text-align: center;
		@include transform(translate(-50%,-50%));
		

		@include g-state( t ) {
			position: relative;
			padding-top: 20px;
			margin: 0 auto 10px;
			width: 55%;
			left: auto;
			top: auto;
			@include transform(translate(0, 0));
		}
		@include g-state( d ) {
			margin: 0 auto;			
		}
	}