$moduleName: "btn";
// Def
button {
    background-color: transparent;
    border: 0 none;
    padding: 0;
    cursor: pointer;
    &:disabled {
        cursor: default !important;
        @include opacity(0.5);
        &:before {
            display: none !important;
        }
        &:hover {
            background-color: $c-link;
            border-color: $c-link;
            color: white;
        }
    }
}

.#{$moduleName}-menu {
    position: absolute;
    left: 1px;
    top: 50%;
    font-size: 24px;
    z-index: 2;
    color: $c-primary;
    @include transform(translate(0, -50%));
    @include transition(all $anim-time);
    &:hover,
    &:focus {
        color: $c-link;
    }
}

.#{$moduleName}-def {
    @extend %text;
    position: relative;
    display: block;
    line-height: 40px;
    padding: 0 20px;
    height: 40px;
    color: white;
    text-align: center;
    font-weight: 600;
    letter-spacing: 1px;
    background-color: $c-link;
    width: 100%;
    margin-bottom: 10px;
    @include transition(background-color $anim-time, color $anim-time linear $anim-time/2);
    &:hover {
        background-color: transparent;
        color: $c-link;
        &:before {
            @include transform(scale(1, 1));
            opacity: 1;
            z-index: 1;
        }
    }
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid $c-link;
        z-index: -1;
        border-radius: inherit;
        opacity: 0;
        @include transform(scale(0.3, 0.3));
        @include transition(all $anim-time*1.5);
        @include transition-timing-function(cubic-bezier(0.3, 0, 0.125, 1));
    }
    @include g-state(d) {
        display: inline-block;
        min-width: 200px;
        width: auto;
        margin-bottom: 0;
    }
}

.#{$moduleName}-def--inverted {
    background-color: white;
    color: $c-link;
    &:hover {
        background-color: transparent;
        color: white;
    }
    &:before {
        border: 1px solid white;
    }
}

.#{$moduleName}-def--delete {
    background-color: $c-red;
    &:hover {
        background-color: transparent;
        color: $c-red;
    }
    &:before {
        border: 1px solid $c-red;
    }
}

.#{$moduleName}-def--secondary {
    background-color: $c-line;
    color: white;
}

.#{$moduleName}-social {
    display: block;
    overflow: visible;
    width: 40px;
    height: 40px;
    line-height: 40px;
    color: white;
    text-align: center;
    @include border-radius(50%);
    @include transition(all $anim-time);
    &:hover {
        background-color: transparent;
        i {
            line-height: 40px;
            height: 40px;
            font-size: 17px;
        }
        &:before {
            @include transform(scale(1, 1) rotate(45deg));
            opacity: 1;
            z-index: 1;
        }
    }
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 2px dotted white;
        z-index: -1;
        border-radius: inherit;
        opacity: 0;
        @include border-radius(50%);
        @include transform(scale(0.1, 0.1) rotate(0deg));
        @include transition(all $anim-time*1.5);
        @include transition-timing-function(cubic-bezier(0.3, -0.8, 0.6, 1.65));
    }
    i {
        color: white;
        font-size: 13px;
        line-height: 41px;
        overflow: hidden;
        height: 40px;
        @include transition(font-size $anim-time)
    }
}

.#{$moduleName}-rnd {
    display: inline-block;
    margin-top: 4px;
    line-height: 50px;
    height: 50px;
    width: 50px;
    text-align: center;
    margin-left: 15px;
    border: 1px solid $c-secondary;
    overflow: hidden;
    background-color: transparent;
    @include border-radius(60px);
    i {
        color: $c-secondary;
        font-size: 30px;
    }
    &:hover {
        border-color: $c-link;
        i {
            border-color: $c-link;
        }
    }
}

.#{$moduleName}-social--fb {
    background-color: $c-fb;
    &:before {
        border-color: $c-fb;
    }
    &:hover i {
        color: $c-fb;
    }
}

.#{$moduleName}-social--gp {
    background-color: $c-gp;
    &:before {
        border-color: $c-gp;
    }
    &:hover i {
        color: $c-gp;
    }
}

.#{$moduleName}-social--tw {
    background-color: $c-tw;
    &:before {
        border-color: $c-tw;
    }
    &:hover i {
        color: $c-tw;
    }
}

.#{$moduleName}-social--li {
    background-color: $c-li;
    &:before {
        border-color: $c-li;
    }
    &:hover i {
        color: $c-li;
    }
}

.#{$moduleName}-social--rss {
    background-color: $c-rss;
    &:before {
        border-color: $c-rss;
    }
    &:hover i {
        color: $c-rss;
    }
}

