.#{$moduleName}-hybrid {
    display: block;
    float: left;
    width: 133px;
    height: 45px;
    margin: 0 5px 20px;
    text-align: center;
    font-size: 15px;
    line-height: 70px;
    overflow: hidden;
    position: relative;
    @include transition(all $anim-time);
    i {
        position: absolute;
        display: block;
        line-height: 1.25;
        text-align: center;
        width: 100%;
        font-size: 22px;
        top: 0;
        color: $c-link;
        @include transition(all $anim-time);
    }
    &:hover {
        color: transparent;
        i {
            font-size: 40px;
            line-height: 45px;
            color: $c-primary;
        }
    }
}

.#{$moduleName}-trafiky {
    @extend .btn-hybrid;
}

.btn-appstore {
    display: block;
    float: left;
    width: 133px;
    height: 45px;
    background-image: url("/templateAssets/images/appstore.png");
    background-repeat: no-repeat;
    background-position: center center;
    margin: 0 5px 20px;
    text-indent: -9999px;
    font-size: 15px;
    color: transparent;
}

.btn-googleplay {
    float: left;
    width: 129px;
    height: 45px;
    display: block;
    background-image: url("/templateAssets/images/googleplay.png");
    background-repeat: no-repeat;
    margin: 0 5px 20px;
    text-indent: -9999px;
    font-size: 15px;
    color: transparent;
}

.btn-delete {
    color: $c-red;
    margin-left: 15px;
}