$moduleName: "product";
// Product je basket detail
.#{$moduleName} {
    border-top: 1px solid $c-line;
    // border-bottom: 1px solid $c-line;
    padding: 10px 25px;
    position: relative;
    &:first-child {
        border-top: 0 none;
    }
    @include g-state(d) {
        overflow: hidden;
        height: 50px;
        line-height: 40px;
        padding: 5px 0px;
    }
}

.#{$moduleName}--finish {
    border-top: 0 none;
    border-bottom: 1px solid $c-line;
    margin-bottom: 20px;
    .product__form {
        display: none;
        text-align: center;
    }
    .product__count {
        display: none;
        text-align: center;
    }
    @include g-state(d) {
        .product__form {
            display: block;
            text-align: left;
        }
        .product__count {
            display: block;
            text-align: left;
        }
    }
}

.#{$moduleName}__img {
    display: block;
    margin: 0 auto 20px;
    @include g-state(d) {
        height: 100%;
        float: left;
        margin: 0;
        margin-right: 5px;
    }
}

.#{$moduleName}__title {
    display: block;
    font-weight: 700;
    font-size: 120%;
    text-align: center;
    a {
        color: $c-primary;
        display: inline-block;
        height: 40px;
        &:hover {
            color: $c-link;
        }
    }
    @include g-state(d) {
        font-size: 100%;
        float: left;
        width: 30%;
        padding-left: 10px;
        text-align: left;
    }
}

.#{$moduleName}__form {
    // text-align: right;
    display: block;
    margin: 0 auto;
    text-align: center;
    label {
        display: block;
        font-weight: 400;
        line-height: 40px;
        text-transform: none;
        text-align: center;
        margin-bottom: 0;
    }
    input {
        display: block;
        height: 40px;
        text-align: center;
        padding: 0 10px;
        color: $c-link;
        border: 0 none;
        margin-bottom: 10px;
        width: 100%;
        font-size: 120%;
        cursor: pointer;
    }
     ::-webkit-calendar-picker-indicator {
        display: block;
        // display: inline;
        appearance: button;
        -webkit-appearance: button;
    }
    ::placeholder {
      color: $c-link;
      opacity: 1;
    }
    :-ms-input-placeholder {
      color: $c-link;
    }
    ::-ms-input-placeholder {
      color: $c-link;
    }
    @include g-state(d) {
        margin: 0;
        float: left;
        width: 30%;
        text-align: left;
        label {
            margin-right: 10px;
            float: left;
            text-align: left;
            margin-bottom: 10px;
        }
        input {
            float: left;
            width: 140px;
            text-align: left;
            margin-bottom: 0;
            font-size: 100%;
        }
    }
}

.#{$moduleName}__price {
    font-weight: 700;
    display: block;
    margin: 0 auto;
    line-height: 40px;
    text-align: center;
    font-size: 20px;
    @include g-state(d) {
        margin: 0;
        width: 15%;
        float: left;
        text-align: right;
        line-height: auto;
    }
}

.#{$moduleName}__count {
    display: block;
    margin: 0 auto;
    text-align: center;
    label {
        display: block;
        font-weight: 400;
        margin: 0 auto;
        line-height: 40px;
        text-transform: none;
        text-align: center;
    }
    input {
        width: 40px;
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
        color: $c-link;
        border: 0 none;
    }
    @include g-state(d) {
        float: left;
        width: 20%;
        text-align: left;
        label {
            margin: 0;
            margin-left: 0;
            text-align: left;
        }
        input {}
    }
}

.#{$moduleName}__remove {
    display: block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    float: right;
    text-transform: uppercase;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    i {
        font-size: 20px;
    }
    @include g-state(d) {
        position: relative;
    }
}

.#{$moduleName} {
    position: relative;
}

.#{$moduleName}-detail {
    @extend %text;
    color: $c-primary;
    margin-bottom: 50px;
    h1 {
        margin-bottom: 40px;
    }
    @include gridle-state(t) {
        padding-top: 50px;
    }
}

.#{$moduleName}-detail__uptitle {
    height: 40px;
    display: block;
    padding: 0 20px;
    font-weight: 700;
    background-color: $c-yellow;
    color: $c-primary;
    line-height: 40px;
    margin-bottom: 40px;
}

.#{$moduleName}-detail__title {
    @extend %header;
    display: inline;
    font-weight: 700;
    margin-right: 15px;
}

.#{$moduleName}-detail__subtitle {
    @extend %header;
    display: inline;
    font-weight: 300;
}

.#{$moduleName}-detail__content {
    @extend %text;
    color: $c-primary;
    font-weight: 400;
    margin-bottom: 20px;
}

.#{$moduleName}-detail__content2 {
    @extend %text;
    font-weight: 400;
    border-top: 1px solid $c-line;
    padding-top: 20px;
    margin-bottom: 30px;
    p {
        color: $c-line;
    }
}

.#{$moduleName}-detail__links {
    margin-bottom: 40px;
}

.#{$moduleName}-detail__img {
    position: relative;
    display: block;
    overflow: hidden;
    @include gridle-state(m) {
        .l-center-VH {
            position: static;
            @include transform(translate(0, 0));
        }
    }
    @include gridle-state(t) {
        height: 610px;
    }
}

.#{$moduleName}-detail__variants {
    border-top: 1px solid $c-line;
    border-bottom: 1px solid $c-line;
    margin-bottom: 50px;
    [class^="l-gr"] {
        border-left: 1px solid $c-line;
        &:first-child {
            border-left: 0 none;
        }
    }
}

.#{$moduleName}-detail__variants--noborder {
    [class^="l-gr"] {
        border-left: 0 none;
    }
}

.#{$moduleName}-detail__variant {
    text-align: center;
    // height: 600px;
    padding-top: 25px;
    padding-bottom: 25px;
}

.#{$moduleName}-detail__variantName {
    @extend %title;
    margin-bottom: 25px;
}

.#{$moduleName}-detail__variantPrice {
    @extend %price;
    margin-bottom: 25px;
}

.#{$moduleName}-detail__variantSubtitle {
    @extend %text;
    margin-bottom: 25px;
}

.#{$moduleName}-detail__variantImg {
    width: 100%;
    max-height: 280px;
    display: block;
    margin-bottom: 30px;
}

.#{$moduleName}-detail__legal {
    border-bottom: 1px solid $c-line;
    padding-bottom: 50px;
    margin-bottom: 40px;
    a {
        margin-right: 30px;
    }
}
