.box{
	position: relative;
	margin-top: 50px;
	margin-bottom: 50px;
	z-index: 1;
	text-align: left;	

	@include g-state('t'){
		width: 830px;
		padding-bottom:30px;
		// padding-top: 60px;
	}
	
	@include g-state('d'){
		// width: 830px;
		padding-bottom:40px;
		// padding-top: 60px;				
	}

	&:hover{
		.box__bg{
			background-color: lighten($c-bg, 2%);			
		}
	}

	p{
		@extend %perex;
		font-weight: 400;
	}
	.author{
		@extend %small;
	}
}
.box__bg{
	display: block;
	background-color: $c-bg-dark;
	position: absolute;	
	right: -20px;
	width: 110%;
	top: 0;
	height: 100%;	
	z-index: -1;
	@include transition(all $anim-time);

	@include g-state('t'){
		right: -50px;
    	width: 930px;
	}
	@include g-state('d'){
		width: 400%;
		right: 0;
	}
}
.box__headerBg{
	background-color: $c-primary;	
	height: 30px;	
	position: absolute;
	top: 0;	
	right: -20px;
	width: 110%;
	
	@include g-state('t'){
		right: -50px;
    	width: 930px;
	}
	@include g-state('d'){
		width: 400%;
		right: 0;
	}
}
.box__header{
	@extend .small;
	line-height: 30px;
	color: $c-bg-dark;
	text-align: left;
	margin-bottom: 30px;
	
	@include g-state('t'){
		margin-bottom: 50px;
	}
	@include g-state('d'){
		margin-bottom: 50px;
	}
}
.box__title{
	@extend %secondTitle;
	margin-bottom: 20px;
}
.box__author{
	@extend %small;
}