.st-container {
	position: relative;
	// overflow: hidden;
	padding-bottom: 150px;
}

.st-pusher {
	position: relative;
	left: 0;
	z-index: 99;
	// height: 100%;	
	@include transition(transform 0.5s);
}

.st-pusher-cover::after, .st-myCover {
	position: fixed;
	top: 0;
	right: 0;
	width: 0;
	height: 0;
	background: rgba(0,0,0,0.3);
	content: '';
	opacity: 0;	
	@include transition(opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s);
	z-index: 180;
}

.st-menu-open .st-pusher::after, .st-menu-open .st-myCover {
	width: 100%;
	height: 100%;
	opacity: 1;
	@include transition(opacity 0.5s);
}
.is-st-menu-opened
{
	overflow: hidden;
}

.st-menu {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 200;
	visibility: hidden;
	overflow: auto;
	width: 300px;
	height: 100%;
	background: $c-menu;
	@include transition(all 0.5s);
}

.st-menu::after {
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.2);
	content: '';
	opacity: 1;
	@include transition(opacity 0.5s);
}

.st-menu-open .st-menu::after {
	width: 0;
	height: 0;
	opacity: 0;
	@include transition(opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s);
}

/* content style */
/* _nav.scss*/

/* Individual effects */

/* Effect 1: Slide in on top */
.st-effect-1.st-menu {
	visibility: visible;	
	@include transform(translate3d(-100%, 0, 0));
	@include transform(translate(-100%, 0));
}

.st-effect-1.st-menu-open .st-effect-1.st-menu {
	visibility: visible;
	@include transform(translate3d(0, 0, 0));
	@include transform(translate(0, 0));
}

.st-effect-1.st-menu::after {
	display: none;
}

/* Effect 2: Reveal */
.st-effect-2.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0));
}

.st-effect-2.st-menu {
	z-index: 1;
}

.st-effect-2.st-menu-open .st-effect-2.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
}

.st-effect-2.st-menu::after {
	display: none;
}

/* Effect 3: Push*/
.st-effect-3.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0));
}

.st-effect-3.st-menu {
	@include transform(translate3d(-100%, 0, 0));
}

.st-effect-3.st-menu-open .st-effect-3.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
}

.st-effect-3.st-menu::after {
	display: none;
}

/* Effect 4: Slide along */
.st-effect-4.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0));
}

.st-effect-4.st-menu {
	z-index: 1;
	@include transform(translate3d(-50%, 0, 0));
}

.st-effect-4.st-menu-open .st-effect-4.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
	@include transform(translate3d(0, 0, 0));
}

.st-effect-4.st-menu::after {
	display: none;
}

/* Effect 5: Reverse slide out */
.st-effect-5.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0));
}

.st-effect-5.st-menu {
	z-index: 1;
	@include transform(translate3d(50%, 0, 0));
}

.st-effect-5.st-menu-open .st-effect-5.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
	@include transform(translate3d(0, 0, 0));
}

/* Effect 6: Rotate pusher */

.st-effect-6.st-container {
	-webkit-perspective: 1500px;
	perspective: 1500px;
}

.st-effect-6 .st-pusher {
	@include transform-origin( 0% 50%);
	@include transform-style(preserve-3d);
}

.st-effect-6.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0) rotateY(-15deg));
}

.st-effect-6.st-menu {
	@include transform(translate3d(-100%, 0, 0));
	@include transform-origin( 100% 50%);
	@include transform-style(preserve-3d);
}

.st-effect-6.st-menu-open .st-effect-6.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
	@include transform(translate3d(-100%, 0, 0) rotateY(15deg));
}

.st-effect-6.st-menu::after {
	display: none;
}

/* Effect 7: 3D rotate in */

.st-effect-7.st-container {
	-webkit-perspective: 1500px;
	perspective: 1500px;
	-webkit-perspective-origin: 0% 50%;
	perspective-origin: 0% 50%;
}

.st-effect-7 .st-pusher {
	@include transform-style(preserve-3d);
}

.st-effect-7.st-menu-open .st-pusher {
	@include transform(translate3d(300px, 0, 0));
}

.st-effect-7.st-menu {
	@include transform(translate3d(-100%, 0, 0) rotateY(-90deg));
	@include transform-origin( 100% 50%);
	@include transform-style(preserve-3d);
}

.st-effect-7.st-menu-open .st-effect-7.st-menu {
	visibility: visible;
	@include transition(transform 0.5s);
	@include transform(translate3d(-100%, 0, 0) rotateY(0deg));
}

