.popup {}

.popup-fullscale {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: $c-link;
    color: white;
    padding: 50px 0;
    text-align: center;
    position: fixed;
    overflow: scroll;
    top: 0;
    left: 0;
    .remodal-close {
        font-weight: 300;
        color: white;
        top: 40px;
        right: 50px;
    }
    .btn-def {
        margin-bottom: 40px;
    }
}

.popup-fullscale__logo {
    padding-top: 20px;
    margin-bottom: 80px;
    text-align: center;
    img {
        margin: 0 auto;
    }
}

.popup-fullscale__title {
    color: white;
    font-size: 40px;
    line-height: 56px;
    margin-bottom: 80px;
    font-weight: 300;
    word-wrap: break-word;
    @include g-state(t) {
        font-size: 50px;
    }
}

.popup-fullscale__p {
    font-size: 20px;
    color: white;
    margin-bottom: 20px;
    a {
        color: white;
        font-weight: bold;
        &:hover {
            text-decoration: underline;
        }
    }
}

.popup-fullscale__p--mb100 {
    margin-bottom: 100px;
}

.popup-fullscale__footer {
    max-width: 1024px;
    margin: 0 auto;
    font-size: 16px;
    color: white;
    margin: 0 auto;
    border-top: 1px solid #ccccff;
    padding-top: 20px;
}