$moduleName: "user";

// Def
	.#{$moduleName}{
		a{
			font-size: 14px;
		}
	}
	.#{$moduleName}__email{
		line-height: 48px;
		color: white;
		font-family: $f-ssans;
		cursor: pointer;
		font-weight: 400;
		font-style: normal;

		&:first-letter{
			text-transform: uppercase;			
		}
		&:hover{
			color: $c-link;
		}

		i{
			float: right;
			font-size: 22px;
			font-style: normal;
		}
	}