$moduleName: "tag";

// Def
	.#{$moduleName}{
		@extend %tags;
		min-height: 20px;
		padding: 0 12px;
		color: white;
		background-color: $c-green-d;
		margin-bottom: 10px;
		display: block;
		max-width: 100%;
		word-wrap: break-word;
	
		@include transition(all $anim-time);

		&:hover,&:focus{
			color: $c-link;
			background-color: transparent;			
		}
	}
	.#{$moduleName}--user{
		font-size: 12px;
		margin-top: 35px;
		padding: 0 30px;
		color: $c-primary;
		background-color: $c-secondary;
	}
